.details {
  &__wrapper {
    display: grid;
    row-gap: 40px;
  }

  @include onTablet {
    row-gap: 48px;
  }
}

.detail {
  &__photos {
    @include pageGrid;
  }

  &__link {
    position: relative;
    cursor: $cursur-mause-main;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.05  ));
  }

  &__title {
    margin-top: 16px;
    margin-bottom: 8px;

    @include boldText;
  }

  &__description {
    @include onDesktop {
      width: 672px;
    }
  }
}
